.stweb__et{
    &-banner-hero {
        height: 300px;
        position: relative;
        margin: 40px 0 16px;
        @media (min-width: 768px){
            margin: 40px 0 24px;
        }
        .container{
            padding: 0 15px;
            height: 100%;
        }
        .swiper-slide{            
            border-radius: 16px;
            overflow: hidden;
        }
        .picture {
            &--bg {
                img {
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    left: 0;
                    right: 0;
                    top: 0;
                    bottom: 0;
                    object-fit: cover;
                    object-position: center;
                }
            }
        }
    
        .content {
            position: relative;
            width: 100%;
            max-width: 188px;
            margin-left: 24px;
            padding-top: 77px;
            z-index: 2;   
            @media (min-width: 992px) {
                margin-left: 40px;
                padding-top: 70px;
                max-width: 403px;                
            }
            .badge{
                color:#FFF;
                font-family: Telefonica;
                font-size: 16px;
                font-style: normal;
                font-weight: 700;
                line-height: 24px;
                margin-bottom: 8px;
            }         
            .title {
                color: #FFF;
                font-family: Telefonica;
                font-size: 28px;
                line-height: 32px;
                font-style: normal;
                font-weight: 300;
                margin: 0;
                .color--cian{
                    color:#019DF4;                    
                }
                @media (min-width: 992px){
                    font-size: 40px;
                    line-height: 48px;
                }
            }
        }
    
        .swiper-arrow{
            width: 100%;
            position: absolute;
            max-width: 1110px;
            padding: 0 15px;
            margin: 0 auto;
            left: 0;
            right: 0;
            top: 50%;
            transform: translate(0, -50%);
            display: flex;
            justify-content: space-between;
            z-index: 1;
        }
        .swiper-button-prev{
            left: 0;
        }
        .swiper-button-next{
            right: 0;
        }
        .swiper-button-next.swiper-button-disabled, 
        .swiper-button-prev.swiper-button-disabled{
            opacity: 1;
        }
        .swiper-button-prev,
        .swiper-button-next {
            position: relative;
            top: 0;
            font-size: 10px;
            width: 28px;
            height: 28px;
            margin-top: 0px;
            border-radius: 50%;
            background-color: #019DF4;
            box-shadow: 0px 0px 4px rgba(0, 0, 0, 0.25);
    
            @media (min-width: 768px) {
                font-size: 12px;
            }
    
            &::after {
                content: inherit;
            }
    
            &.swiper-button-disabled {
                background-color: #fff;
                box-shadow: 0px 0px 1px rgba(0, 0, 0, 0.25);
    
                >.icon {
                    path {
                        stroke: #80CEF9
                        ;
                    }
                }
            }
    
            >.icon {
    
                path {
                    stroke: #fff;
                }
            }
            &:focus{
                outline: none;
            }
        }
    }
    &-tabs-plans{
        margin-bottom: 40px;
        @media (min-width:768px){
            margin-bottom: 60px;
        }
        .tab-panel {    
            .tab-content .tab-pane{
                padding-bottom: 0;
            }
            .tabs-wrapper {
                @media (max-width:768px) {
                    overflow: overlay;
                    box-shadow: 0px 0px 18px rgba(56, 100, 166, 0.2);
                }
    
                .nav-tabs {
                    background: #FFFFFF;
                    box-shadow: 0px 0px 18px rgba(56, 100, 166, 0.2);
                    border-radius: 8px;
                    padding: 4px;
                    border: 0;
                    width: 1108px;
                    @media (min-width: 768px) {
                        width: 100%;
                    }
                    .nav-item {
                        border: 0;
                        padding: 4px;
    
                        .nav-link {
                            background: #F5F5F5;
                            border-radius: 8px;
                            font-weight: 700;
                            font-size: 18px;
                            line-height: 24px;
                            padding: 16px;
                            color: #0B2739;
                            @media (max-width: 993px) and (min-width: 768px) {
                                font-size: 14px;
                                line-height: 20px;
                                padding: 12px 10px;
                            }
                        }
    
                        &.active,
                        &:hover {
                            .nav-link {
                                background: #0B2739;
                                color: #FFFFFF;
                            }
                        }
                    }
                }
            }
        }
    }    

    &-favorite-plan{
        display: flex;
        padding: 40px 0px 60px;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 10px;
        background: #F6F6F6;
        @media (min-width: 992px){
            padding: 60px 0px 80px;
        }
        .col__plan{
            .title{
                color: #0B2739;
                font-size: 22px;
                line-height: 24px;
                font-weight: 400;
                margin-bottom: 16px;
                @media (min-width: 992px){
                    font-size: 28px;
                    line-height: 32px;
                }
            }
            .text{
                color: #313235;
                font-weight: 400;
                font-size: 16px;
                line-height: 24px;
                margin-bottom: 24px;
                @media (min-width: 992px){
                    font-size: 18px;
                }
            }
            .list{
                display: flex;
                align-items: center;
                flex-wrap: wrap;
                @media (min-width: 1200px) {
                    flex-wrap: nowrap;
                }
                .item{
                    width: 100%;
                    @media (min-width: 1200px){
                        width: auto;
                    }
                    &:nth-child(1){
                        padding-left: 0;
                        padding-right: 24px;
                        margin-bottom: 16px;
                        @media (min-width: 1200px){
                            margin-bottom: 0;
                        }
                    }
                    .text{
                        color: #50535A;
                        font-size: 18px;
                        font-weight: 700;
                        line-height: 24px;
                        margin-bottom: 0;
                    }
                    .btn-group{
                        display: flex;
                        align-items: center;
                        .stweb-btn{
                            font-size: 16px;                            
                            width: 100%;
                            padding-top: 12px;
                            padding-bottom: 12px;
                            &:nth-child(1){
                                margin-right: 16px;
                            }
                            @media (min-width: 768px) {
                                width: auto;
                            }
                        }
                    }
                }
            }

            .picture{
                img{
                    width: 100%;
                    margin-top: 48px;
                    @media (min-width: 768px) {                    
                        float: right;
                        margin-top: 0;
                        width: auto;
                    }
                }
            }
        }
    }
    &-favorite-device{
        padding: 40px 0 24px;
        @media (min-width: 768px){
            padding: 60px 0 56px;
        }
        .title{
            color:#313235;
            text-align: center;
            font-size: 28px;
            line-height: 32px;
            font-weight: 300;
            margin-bottom: 16px;
            @media (min-width: 768px){
                font-size: 38px;
                line-height: 46px;
            }
        }
        .text{
            color:#313235;
            text-align: center;
            font-size: 18px;
            line-height: 24px;
            font-weight: 400;
            margin-bottom: 0;
            @media (min-width: 768px){
                font-size: 20px;
                line-height: 28px;
            }
        }
        .device{
            width: 100%;
            max-width: 800px;
            margin: 0 auto;
            padding: 32px 0 16px;
            @media (min-width: 768px){
                padding: 40px 0;
            }
            .row{
                justify-content: center;
            }
            .item{
                &:first-child{
                    margin-bottom: 16px;
                    @media (min-width: 768px) {
                        margin-bottom: 0;
                    }
                }
                .icon{
                    display: block;
                    text-align: center;
                    svg{
                        font-size: 60px;
                        @media (min-width: 768px){
                            font-size: 80px;
                        }
                    }
                }
                .text{
                    display: block;
                    text-align: center;
                    margin-top: 8px;
                    color:#86888C;
                    font-size: 16px;
                    line-height: 24px;
                    font-weight: 400;
                    @media (min-width: 768px){
                        font-size: 20px;
                        line-height: 28px;
                    }
                }
            }
        }
        .benefits{
            width: 100%;
            max-width: 620px;
            margin: 0 auto;
            .title{
                color:#86888C;
                text-align: center;
                font-size: 18px;
                line-height: 24px;
                font-weight: 300;
                margin-bottom: 0;
                @media (min-width: 768px){
                    font-size: 20px;
                    line-height: 28px;
                }
            }
            .swiper-container{
                padding: 23px;
            }
            .plans-include {
                width: 100%;            
                margin: 0 auto;
                display: flex;
                padding: 20px;
                &__item {
                    background: #FFFFFF;
                    box-shadow: 0px 7px 8px rgba(11, 39, 57, 0.02), 0px 5px 22px rgba(11, 39, 57, 0.12);
                    border-radius: 16px;
                    display: flex;
                    align-items: center;
                    width: 272px;
                    height: 116px;
                    padding: 15px;
    
                    .picture {
                        margin-right: 8px;
                    }
    
                    .content {
                        width: 182px;
                        .title {
                            font-weight: 700;
                            font-size: 14px;
                            line-height: 20px;
                            letter-spacing: 0.12px;
                            color: #0B2739;
                            margin-bottom: 0;
                            text-align: left;
                            padding: 0;
                        }
    
                        .text {
                            font-weight: 400;
                            font-size: 14px;
                            line-height: 20px;
                            letter-spacing: 0.12px;
                            color: #50535A;
                        }
                    }
                }
            }
        }
    }
}